import { Component, ViewChild, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ProductService } from "../../providers/product.services";
import { environment } from "../../../environments/environment";
import { ToastController } from "@ionic/angular";
import { MatDialog } from "@angular/material/dialog";
import { FeedBackSuccessModal } from "./feedback-success";

@Component({
  selector: "page-feedback",
  templateUrl: "feedback.html",
  styleUrls: ["./feedback.scss"],
})
export class FeedbackPage implements OnInit {
  constructor(
    private _ProductService: ProductService,
    public toastController: ToastController,
    public dialog: MatDialog,
    private router: Router,
    public _toast: ToastController
  ) {}

  public searchproductList: any = [];
  public feedback: any;
  ngOnInit() {
    this.generateForm();
  }

  getRatingValue(property) {
    switch (property) {
      case "a":
        return 1;
      case "b":
        return 2;
      case "c":
        return 3;
      case "d":
        return 4;
      case "e":
        return 5;
      case "f":
        return 0;
    }
  }

  async saveFeedback() {
    let productRating;
    for (const property in this.productRating) {
      console.log(this.productRating[property]);
      if (this.productRating[property] == true) {
        console.log("res : ", property);
        productRating = this.getRatingValue(property);
      }
    }

    let _paymentRating;
    for (const property in this.paymentRating) {
      console.log(this.paymentRating[property]);
      if (this.paymentRating[property] == true) {
        console.log("res : ", property);
        _paymentRating = this.getRatingValue(property);
      }
    }

    let serviceRating;
    for (const property in this.serviceRating) {
      console.log(this.serviceRating[property]);
      if (this.serviceRating[property] == true) {
        console.log("res : ", property);
        serviceRating = this.getRatingValue(property);
      }
    }

    let appRating;
    for (const property in this.applicationRating) {
      console.log(this.applicationRating[property]);
      if (this.applicationRating[property] == true) {
        console.log("res : ", property);
        appRating = this.getRatingValue(property);
      }
    }

    if (!this.isRated) {
      const toast = await this._toast.create({
        message: "Please Provide your feedback",
        duration: 3000,
        color: "warning",
        position: "bottom",
        animated: true,
      });
      toast.present();
      return;
    }

    console.log("productRating", productRating);
    this._ProductService
      .feedback({
        UserId: localStorage.getItem("userId"),
        productRating: productRating,
        PaymentRating: _paymentRating,
        ServiceRating: serviceRating,
        ApplicationRating: appRating,
        feedback: this.feedback,
      })
      .subscribe(async (data: any) => {
        console.log(data);

        this.generateForm();
        this.dialog.open(FeedBackSuccessModal);
        // this.router.navigateByUrl('/home')
      });
  }

  public SubCategoryList: any;
  public baseUrl = environment.baseUrl + "/";
  public searchWord: any;

  getColor(index: number, rating) {
    if (this.isAboveRating(index, rating)) {
      return "#DDDDDD";
    }

    return "gold";
  }

  public isCommentBoxEnable: Boolean = false;
  public boxId: any = "";

  public productRating;
  public paymentRating;
  public serviceRating;
  public applicationRating;

  generateForm() {
    this.productRating = {
      a: false,
      b: false,
      c: false,
      d: false,
      e: false,
      f: true,
      rating: 0,
    };

    this.paymentRating = {
      a: false,
      b: false,
      c: false,
      d: false,
      e: false,
      f: true,
      rating: 0,
    };

    this.serviceRating = {
      a: false,
      b: false,
      c: false,
      d: false,
      e: false,
      f: true,
      rating: 0,
    };

    this.applicationRating = {
      a: false,
      b: false,
      c: false,
      d: false,
      e: false,
      f: true,
      rating: 0,
    };

    this.feedback = "";
  }

  public isRated = false;
  rate(index, id) {
    this.isRated = true;
    console.log(index, id);
    this[id]["rating"] = index;
    switch (index) {
      case 1:
        this[id]["a"] = true;
        this[id]["b"] = false;
        this[id]["c"] = false;
        this[id]["d"] = false;
        this[id]["e"] = false;
        this[id]["f"] = false;

        break;
      case 2:
        this[id]["a"] = false;
        this[id]["b"] = true;
        this[id]["c"] = false;
        this[id]["d"] = false;
        this[id]["e"] = false;
        this[id]["f"] = false;
        break;
      case 3:
        this[id]["a"] = false;
        this[id]["b"] = false;
        this[id]["c"] = true;
        this[id]["d"] = false;
        this[id]["e"] = false;
        this[id]["f"] = false;
        break;
      case 4:
        this[id]["a"] = false;
        this[id]["b"] = false;
        this[id]["c"] = false;
        this[id]["d"] = true;
        this[id]["e"] = false;
        this[id]["f"] = false;
        break;
      case 5:
        this[id]["a"] = false;
        this[id]["b"] = false;
        this[id]["c"] = false;
        this[id]["d"] = false;
        this[id]["e"] = true;
        this[id]["f"] = false;
        break;
      default:
        this[id]["a"] = false;
        this[id]["b"] = false;
        this[id]["c"] = false;
        this[id]["d"] = false;
        this[id]["e"] = false;
        this[id]["f"] = true;
    }
  }

  isAboveRating(index: number, rating) {
    return index > rating;
  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true;
  }

  backToNormal() {
    this.showSearchBox = false;
  }

  public refershDefault = {
    target: {
      complete: function () {
        return true;
      },
    },
  };
  getSubCategoryList(event = this.refershDefault) {
    this._ProductService.listSubCategory().subscribe((data: any) => {
      this.SubCategoryList = data.SubCategoryList;
      console.log(data, "--------------------------", this.SubCategoryList);

      if (event) {
      }
      setTimeout(() => {
        console.log("Async operation has ended");
        event.target.complete();
      }, 1500);
    });
  }
}
